@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.listagem .titulo-categoria:first-child {
    margin-top: 20px;

}
.listagem .titulo-categoria {
    display: block;
    font-size: 25px;
    margin: 40px 0 40px 0;
    padding: 10px 0;
    text-align: center;
    transition: none;
    border: none;
    font-weight: normal;
}
.listagem .titulo-categoria strong {
    font-weight: 200;
    text-transform: uppercase;
    background: white;
    padding: 0 30px;
    color: black;
    letter-spacing: 4px;
    font-family: montserrat;
}

.borda-principal {
    border-width: 2px 0 0;
    border-style: solid;
}

.listagem .titulo-categoria::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 1px;
    margin: auto;
    background: #e6e0df;
    z-index: -1;
}

.listagem .titulo-categoria:after, #listagemProdutos.listagem>.titulo:after {
    content: "";
    background: #e6e0df;
    width: 96%;
    height: 1px;
    display: block;
    margin: -15px auto;
}

.container {
    padding: 5px;
}

.row{
    margin-left: 5%;
    margin-top: -6%;
}

.row:after {
    content: "";
    display: table;
    clear: both
}
.column {
    float: left;
    width: 33.3%;
    margin-bottom: 16px;
    padding: 0 8px;
    margin-top: 6%;
}
.card {
    margin: 8px;
}

.card-container {
    padding: 0 16px;
}

.card-container::after,
.row::after {
    content: "";
    clear: both;
    display: table;
}
  
.column-66 {
    float: left;
    width: 66.66666%;
    padding: 20px;
}
  
.column-33 {
    float: left;
    width: 33.33333%;
    padding: 20px;
}

.large-font {
    font-size: 40px;
    color: black;
    margin-top: 13%;
    background: linear-gradient(to right, red , yellow);
}

.description-x{
    text-align: justify;
    margin-top: 5%;
    margin-left: 0%;
    margin-right: 9%;
    font-weight: 300;
}

.subdescription{
    margin-top: 5%;
    text-align: justify;
    font-weight: 300;
    margin-left: 22%;
}

img {
    margin-left: -30%;
    /* width: 100%; */
}

img .logo-mundo{
    margin-left: -30%;
    width: 100%;
}



@media screen and (max-width: 900px) {

    .column-66,
    .column-33 {
      width: 100%;
      text-align: center;
    }
    .large-font {
        font-size: 42px;
        color: black;
        margin-top: 43%;
        background: white;
    }

    .description-x {
        text-align: justify;
        margin-top: 10%;
        margin-left: 1%;
    }

    img {
        margin: auto;
        width: 60%;
        margin-left: 0px;
    }
    
    /* .container {
        padding: 4px;
    } */

    .card-container {
        padding: 0;
    }
    
}

@media screen and (max-width: 650px) {
    .column {
        width: 100%;
        display: block;
    }
}

.App {
    font-family: Montserrat;
    font-weight: 600;
    text-align: center;
}

a {
    text-decoration: none;
    color: black;
}


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.header {
    display: flex;
    background: white;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 80px;
    border-style: solid;
    border-bottom-width: 1px;
    border-top-width: 0;
    border-right-width: 0;
    border-left-width: 0;
}

.logo-nav {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo {
    width: 45px;
    height: 45px;
}

.nav-options {
    padding-left: 25px;
    display: grid;
    grid-template-columns: repeat(8, auto);
    grid-gap: 50px;
    list-style-type: none;
}

.mobile-option {
    display: none;
}

.option :hover {
    color: black;
}

.mobile-menu {
    display: none;
}

@media (max-width: 648px) {
    /*Mobile View */
    .header {
        padding: 0px 10px;
    }
    .logo {
        width: 45px;
        height: 45px;
    }
    .nav-options {
        display: flex;
        width: 100%;
        height: 73%;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }
    .nav-options.active {
        background: #808080;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        align-content: center;
        padding-left: 0px;
    }
    .menu-icon {
        width: 45px;
        height: 45px;
    }
    .option {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }
    .carousel .control-dots {
        bottom: -23px;
        margin: 10px 0;
        padding: 10px 0px 0px 0px;
        text-align: center;
        width: 100%;
        z-index: 1;
    }
   
}

.mobile-menu {
    display: block;
}

.app-footer {
    left: 0;
    bottom: 0;
    width: 100%;
    background: white;
    text-align: center;
    height: 8%;
    color: black;
    padding: 53px 0;
    border-top: 1px solid black;
    margin-top: 5%;
}

.app-footer__message {
    text-align: justify;
    font-weight: 300;
}

/* ----------- iPad Pro ----------- */

@media only screen and (min-width: 1024px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
    .container {
        padding: 50px;
    }
    /* .row {
        margin-left: 0%;
    } */
    .nav-options {
        padding-left: 25px;
        display: grid;
        grid-template-columns: repeat(8, auto);
        grid-gap: 18px;
        list-style-type: none;
    }
}

/* ----------- iPad  ----------- */

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    .nav-options {
        display: grid;
        grid-template-columns: repeat(8, auto);
        grid-gap: 13px;
        list-style-type: none;
        margin-right: 5%;
    }
    .container {
        padding: 0px
    }
}
